import React from 'react';
import { Link } from 'react-router-dom';
import './Navigation.scss';

const Navigation = () => {
    return (
        <div className='navi'>
            <Link to="/" className="lnk">HOME</Link>
            <Link to="/about" className="lnk">ABOUT</Link>
        </div>
    );
}; 

export default Navigation;